.gradient-border-shadow {
  position: relative;
}
.gradient-border-shadow > * {
  position: relative;
  z-index: 5;
}
.gradient-border-shadow::after, .gradient-border-shadow::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 15px;
}
.gradient-border-shadow::before {
  background: linear-gradient(200deg, #e05976, #9a60c2);
  z-index: 2;
}
.gradient-border-shadow::after {
  z-index: 3;
  background-color: #f6f6f6;
  background-clip: padding-box; /* !importanté */
  border: solid 3px transparent; /* !importanté */
  box-sizing: border-box;
}
.gradient-border-shadow::after, .gradient-border-shadow::before {
  top: 15%;
  left: 15%;
}

